
Template.__checkName("afTextarea_materialize");
Template["afTextarea_materialize"] = new Template("Template.afTextarea_materialize", (function() {
  var view = this;
  return HTML.TEXTAREA(HTML.Attrs(function() {
    return Spacebars.attrMustache(view.lookup("atts"));
  }, {
    value: function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("."), "value"));
    }
  }));
}));
