
Template.__checkName("afCheckboxGroup_materialize");
Template["afCheckboxGroup_materialize"] = new Template("Template.afCheckboxGroup_materialize", (function() {
  var view = this;
  return HTML.DIV(HTML.Attrs({
    "class": "af-checkbox-group collection"
  }, function() {
    return Spacebars.attrMustache(view.lookup("dsk"));
  }), "\n    ", Blaze.Each(function() {
    return Spacebars.call(view.lookup("items"));
  }, function() {
    return [ "\n    ", HTML.P("\n      ", HTML.INPUT(HTML.Attrs({
      type: "checkbox",
      value: function() {
        return Spacebars.mustache(view.lookup("value"));
      }
    }, function() {
      return Spacebars.attrMustache(view.lookup("itemAtts"));
    })), "\n      ", HTML.LABEL({
      "for": function() {
        return Spacebars.mustache(Spacebars.dot(view.lookup("itemAtts"), "id"));
      }
    }, Blaze.View("lookup:label", function() {
      return Spacebars.mustache(view.lookup("label"));
    })), "\n    "), "\n    " ];
  }), "\n  ");
}));
