
Template.__checkName("afInputEmail_materialize");
Template["afInputEmail_materialize"] = new Template("Template.afInputEmail_materialize", (function() {
  var view = this;
  return HTML.INPUT(HTML.Attrs({
    type: "email",
    value: function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("."), "value"));
    }
  }, function() {
    return Spacebars.attrMustache(view.lookup("atts"));
  }));
}));
