
Template.__checkName("afIcon_materialize");
Template["afIcon_materialize"] = new Template("Template.afIcon_materialize", (function() {
  var view = this;
  return HTML.I({
    "class": "material-icons prefix"
  }, Blaze.View("lookup:..icon", function() {
    return Spacebars.mustache(Spacebars.dot(view.lookup("."), "icon"));
  }));
}));
