
Template.__checkName("afInputDateTime_materialize");
Template["afInputDateTime_materialize"] = new Template("Template.afInputDateTime_materialize", (function() {
  var view = this;
  return HTML.INPUT(HTML.Attrs({
    type: "datetime",
    value: function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("."), "value"));
    }
  }, function() {
    return Spacebars.attrMustache(view.lookup("atts"));
  }));
}));
