
Template.__checkName("afSwitch");
Template["afSwitch"] = new Template("Template.afSwitch", (function() {
  var view = this;
  return HTML.DIV({
    "class": "switch"
  }, "\n    ", HTML.LABEL({
    "for": function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("atts"), "id"));
    }
  }, "\n      ", Spacebars.With(function() {
    return Spacebars.call(Spacebars.dot(view.lookup("atts"), "falseLabel"));
  }, function() {
    return Blaze.View("lookup:.", function() {
      return Spacebars.mustache(view.lookup("."));
    });
  }, function() {
    return "Off";
  }), "\n      ", HTML.INPUT(HTML.Attrs({
    type: "checkbox",
    value: function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("."), "value"));
    }
  }, function() {
    return Spacebars.attrMustache(view.lookup("atts"));
  })), "\n      ", HTML.Raw('<span class="lever"></span>'), "\n      ", Spacebars.With(function() {
    return Spacebars.call(Spacebars.dot(view.lookup("atts"), "trueLabel"));
  }, function() {
    return Blaze.View("lookup:.", function() {
      return Spacebars.mustache(view.lookup("."));
    });
  }, function() {
    return "On";
  }), "\n    "), "\n  ");
}));
