
Template.__checkName("afInputSearch_materialize");
Template["afInputSearch_materialize"] = new Template("Template.afInputSearch_materialize", (function() {
  var view = this;
  return HTML.INPUT(HTML.Attrs({
    type: "search",
    value: function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("."), "value"));
    }
  }, function() {
    return Spacebars.attrMustache(view.lookup("atts"));
  }));
}));
