
Template.__checkName("afFormGroup_materialize");
Template["afFormGroup_materialize"] = new Template("Template.afFormGroup_materialize", (function() {
  var view = this;
  return HTML.DIV({
    "class": "row"
  }, "\n        ", HTML.DIV({
    "class": function() {
      return [ Blaze.If(function() {
        return Spacebars.call(view.lookup("addInputField"));
      }, function() {
        return "input-field";
      }), " col s12" ];
    }
  }, "\n            ", Blaze.If(function() {
    return Spacebars.call(Spacebars.dot(view.lookup("."), "afFieldInputAtts", "icon"));
  }, function() {
    return [ "\n                ", Blaze._TemplateWith(function() {
      return Spacebars.call(Spacebars.dot(view.lookup("."), "afFieldInputAtts"));
    }, function() {
      return Spacebars.include(view.lookupTemplate("afIcon_materialize"));
    }), "\n            " ];
  }), "\n            ", Blaze.If(function() {
    return Spacebars.call(view.lookup("skipLabel"));
  }, function() {
    return [ "\n                ", Blaze._TemplateWith(function() {
      return Spacebars.call(Spacebars.dot(view.lookup("."), "afFieldInputAtts"));
    }, function() {
      return Spacebars.include(view.lookupTemplate("afFieldInput"));
    }), "\n            " ];
  }, function() {
    return [ "\n                ", Blaze._TemplateWith(function() {
      return Spacebars.call(Spacebars.dot(view.lookup("."), "afFieldInputAtts"));
    }, function() {
      return Spacebars.include(view.lookupTemplate("afFieldInput"));
    }), "\n                ", Blaze._TemplateWith(function() {
      return Spacebars.call(view.lookup("."));
    }, function() {
      return Spacebars.include(view.lookupTemplate("afLabel_materialize"));
    }), "\n            " ];
  }), "\n            ", Blaze.If(function() {
    return Spacebars.dataMustache(view.lookup("afFieldIsInvalid"), Spacebars.kw({
      name: Spacebars.dot(view.lookup("."), "name")
    }));
  }, function() {
    return [ "\n                ", HTML.DIV({
      "class": "red-text"
    }, "\n                    ", HTML.I({
      "class": "mdi-alert-warning"
    }), " ", Blaze.View("lookup:afFieldMessage", function() {
      return Spacebars.makeRaw(Spacebars.mustache(view.lookup("afFieldMessage"), Spacebars.kw({
        name: Spacebars.dot(view.lookup("."), "name")
      })));
    }), "\n                "), "\n            " ];
  }), "\n        "), "\n    ");
}));
