
Template.__checkName("afInputHidden_materialize");
Template["afInputHidden_materialize"] = new Template("Template.afInputHidden_materialize", (function() {
  var view = this;
  return HTML.INPUT(HTML.Attrs({
    type: "hidden",
    value: function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("."), "value"));
    }
  }, function() {
    return Spacebars.attrMustache(view.lookup("atts"));
  }));
}));
