
Template.__checkName("afInputDate_materialize");
Template["afInputDate_materialize"] = new Template("Template.afInputDate_materialize", (function() {
  var view = this;
  return HTML.INPUT(HTML.Attrs({
    type: "date",
    value: function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("."), "value"));
    }
  }, function() {
    return Spacebars.attrMustache(view.lookup("atts"));
  }));
}));
