
Template.__checkName("afInputReset_materialize");
Template["afInputReset_materialize"] = new Template("Template.afInputReset_materialize", (function() {
  var view = this;
  return HTML.INPUT(HTML.Attrs({
    type: "reset",
    value: function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("."), "value"));
    }
  }, function() {
    return Spacebars.attrMustache(view.lookup("attsPlusBtnClassMaterializeCss"), view.lookup("atts"));
  }));
}));
