
Template.__checkName("afPickadate");
Template["afPickadate"] = new Template("Template.afPickadate", (function() {
  var view = this;
  return HTML.INPUT(HTML.Attrs({
    type: "date",
    "data-value": function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("."), "value"));
    },
    value: function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("."), "value"));
    },
    checked: "checked/"
  }, function() {
    return Spacebars.attrMustache(view.lookup("atts"));
  }));
}));
