
Template.__checkName("afInputWeek_materialize");
Template["afInputWeek_materialize"] = new Template("Template.afInputWeek_materialize", (function() {
  var view = this;
  return HTML.INPUT(HTML.Attrs({
    type: "week",
    value: function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("."), "value"));
    }
  }, function() {
    return Spacebars.attrMustache(view.lookup("atts"));
  }));
}));
