
Template.__checkName("afInputDateTimeLocal_materialize");
Template["afInputDateTimeLocal_materialize"] = new Template("Template.afInputDateTimeLocal_materialize", (function() {
  var view = this;
  return HTML.INPUT(HTML.Attrs({
    type: "datetime-local",
    value: function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("."), "value"));
    }
  }, function() {
    return Spacebars.attrMustache(view.lookup("atts"));
  }));
}));
