
Template.__checkName("afInputText_materialize");
Template["afInputText_materialize"] = new Template("Template.afInputText_materialize", (function() {
  var view = this;
  return HTML.INPUT(HTML.Attrs({
    type: "text",
    value: function() {
      return Spacebars.mustache(view.lookup("value"));
    }
  }, function() {
    return Spacebars.attrMustache(view.lookup("atts"));
  }));
}));
