
Template.__checkName("afInputRange_materialize");
Template["afInputRange_materialize"] = new Template("Template.afInputRange_materialize", (function() {
  var view = this;
  return HTML.P({
    "class": "range-field"
  }, "\n    ", HTML.INPUT(HTML.Attrs({
    type: "range",
    value: function() {
      return Spacebars.mustache(view.lookup("value"));
    }
  }, function() {
    return Spacebars.attrMustache(view.lookup("atts"));
  })), "\n  ");
}));
