
Template.__checkName("afInputTime_materialize");
Template["afInputTime_materialize"] = new Template("Template.afInputTime_materialize", (function() {
  var view = this;
  return HTML.INPUT(HTML.Attrs({
    type: "time",
    value: function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("."), "value"));
    }
  }, function() {
    return Spacebars.attrMustache(view.lookup("atts"));
  }));
}));
