
Template.__checkName("afInputFile_materialize");
Template["afInputFile_materialize"] = new Template("Template.afInputFile_materialize", (function() {
  var view = this;
  return HTML.INPUT(HTML.Attrs({
    type: "file",
    value: function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("."), "value"));
    }
  }, function() {
    return Spacebars.attrMustache(view.lookup("atts"));
  }));
}));
