
Template.__checkName("afInputPassword_materialize");
Template["afInputPassword_materialize"] = new Template("Template.afInputPassword_materialize", (function() {
  var view = this;
  return HTML.INPUT(HTML.Attrs({
    type: "password",
    value: function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("."), "value"));
    }
  }, function() {
    return Spacebars.attrMustache(view.lookup("atts"));
  }));
}));
