
Template.__checkName("afInputUrl_materialize");
Template["afInputUrl_materialize"] = new Template("Template.afInputUrl_materialize", (function() {
  var view = this;
  return HTML.INPUT(HTML.Attrs({
    type: "url",
    value: function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("."), "value"));
    }
  }, function() {
    return Spacebars.attrMustache(view.lookup("atts"));
  }));
}));
