
Template.__checkName("afInputColor_materialize");
Template["afInputColor_materialize"] = new Template("Template.afInputColor_materialize", (function() {
  var view = this;
  return HTML.INPUT(HTML.Attrs({
    type: "color",
    value: function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("."), "value"));
    }
  }, function() {
    return Spacebars.attrMustache(view.lookup("atts"));
  }));
}));
