
Template.__checkName("afRadioGroupInline_materialize");
Template["afRadioGroupInline_materialize"] = new Template("Template.afRadioGroupInline_materialize", (function() {
  var view = this;
  return HTML.DIV(HTML.Attrs({
    "class": "af-radio-group"
  }, function() {
    return Spacebars.attrMustache(view.lookup("dsk"));
  }), "\n        ", HTML.P("\n            ", Blaze.Each(function() {
    return Spacebars.call(Spacebars.dot(view.lookup("."), "items"));
  }, function() {
    return [ "\n                ", HTML.SPAN("\n                    ", HTML.INPUT(HTML.Attrs({
      type: "radio",
      value: function() {
        return Spacebars.mustache(Spacebars.dot(view.lookup("."), "value"));
      }
    }, function() {
      return Spacebars.attrMustache(view.lookup("itemAtts"));
    })), "\n                    ", HTML.LABEL({
      "for": function() {
        return Spacebars.mustache(Spacebars.dot(view.lookup("itemAtts"), "id"));
      }
    }, Blaze.View("lookup:..label", function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("."), "label"));
    })), "\n                "), "\n            " ];
  }), "\n        "), "\n    ");
}));
