
Template.__checkName("afInputButton_materialize");
Template["afInputButton_materialize"] = new Template("Template.afInputButton_materialize", (function() {
  var view = this;
  return HTML.INPUT(HTML.Attrs({
    type: "button",
    value: function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("."), "value"));
    }
  }, function() {
    return Spacebars.attrMustache(view.lookup("attsPlusBtnClassMaterializeCss"), view.lookup("atts"));
  }));
}));
