
Template.__checkName("afQuickField_materialize");
Template["afQuickField_materialize"] = new Template("Template.afQuickField_materialize", (function() {
  var view = this;
  return Blaze.If(function() {
    return Spacebars.dataMustache(view.lookup("isGroup"), Spacebars.kw({
      atts: view.lookup(".")
    }));
  }, function() {
    return [ "\n    ", Spacebars.include(view.lookupTemplate("afObjectField_materialize")), "\n  " ];
  }, function() {
    return [ "\n    ", Blaze.If(function() {
      return Spacebars.dataMustache(view.lookup("isFieldArray"), Spacebars.kw({
        atts: view.lookup(".")
      }));
    }, function() {
      return [ "\n      ", Spacebars.include(view.lookupTemplate("afArrayField_materialize")), "\n    " ];
    }, function() {
      return [ "\n      ", Blaze._TemplateWith(function() {
        return Spacebars.call(view.lookup("groupAtts"));
      }, function() {
        return Spacebars.include(view.lookupTemplate("afFormGroup_materialize"));
      }), "\n    " ];
    }), "\n  " ];
  });
}));
