
Template.__checkName("afInputNumber_materialize");
Template["afInputNumber_materialize"] = new Template("Template.afInputNumber_materialize", (function() {
  var view = this;
  return HTML.INPUT(HTML.Attrs({
    type: "number",
    value: function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("."), "value"));
    }
  }, function() {
    return Spacebars.attrMustache(view.lookup("atts"));
  }));
}));
