
Template.__checkName("afInputSubmit_materialize");
Template["afInputSubmit_materialize"] = new Template("Template.afInputSubmit_materialize", (function() {
  var view = this;
  return HTML.INPUT(HTML.Attrs({
    type: "submit",
    value: function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("."), "value"));
    }
  }, function() {
    return Spacebars.attrMustache(view.lookup("attsPlusBtnClassMaterializeCss"), view.lookup("atts"));
  }));
}));
