
Template.__checkName("afContenteditable_materialize");
Template["afContenteditable_materialize"] = new Template("Template.afContenteditable_materialize", (function() {
  var view = this;
  return [ Blaze.View("lookup:getValue", function() {
    return Spacebars.mustache(view.lookup("getValue"), Spacebars.dot(view.lookup("."), "value"));
  }), "\n  ", HTML.DIV(HTML.Attrs({
    contenteditable: "true"
  }, function() {
    return Spacebars.attrMustache(view.lookup("atts"));
  })) ];
}));
