
Template.__checkName("afCheckbox_materialize");
Template["afCheckbox_materialize"] = new Template("Template.afCheckbox_materialize", (function() {
  var view = this;
  return HTML.P("\n    ", HTML.INPUT(HTML.Attrs({
    type: "checkbox",
    value: function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("."), "value"));
    }
  }, function() {
    return Spacebars.attrMustache(view.lookup("atts"));
  })), "\n    ", HTML.LABEL({
    "for": function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("atts"), "id"));
    }
  }, Blaze.View("lookup:afFieldLabelText", function() {
    return Spacebars.mustache(view.lookup("afFieldLabelText"), Spacebars.kw({
      name: Spacebars.dot(view.lookup("."), "name")
    }));
  })), "\n  ");
}));
