
Template.__checkName("afInputTel_materialize");
Template["afInputTel_materialize"] = new Template("Template.afInputTel_materialize", (function() {
  var view = this;
  return HTML.INPUT(HTML.Attrs({
    type: "tel",
    value: function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("."), "value"));
    }
  }, function() {
    return Spacebars.attrMustache(view.lookup("atts"));
  }));
}));
