
Template.__checkName("afInputMonth_materialize");
Template["afInputMonth_materialize"] = new Template("Template.afInputMonth_materialize", (function() {
  var view = this;
  return HTML.INPUT(HTML.Attrs({
    type: "month",
    value: function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("."), "value"));
    }
  }, function() {
    return Spacebars.attrMustache(view.lookup("atts"));
  }));
}));
