
Template.__checkName("afInputImage_materialize");
Template["afInputImage_materialize"] = new Template("Template.afInputImage_materialize", (function() {
  var view = this;
  return HTML.INPUT(HTML.Attrs({
    type: "image",
    value: function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("."), "value"));
    }
  }, function() {
    return Spacebars.attrMustache(view.lookup("atts"));
  }));
}));
