
Template.__checkName("autoForm_materialize");
Template["autoForm_materialize"] = new Template("Template.autoForm_materialize", (function() {
  var view = this;
  return Blaze.Unless(function() {
    return Spacebars.call(view.lookup("afDestroyUpdateForm"));
  }, function() {
    return [ "\n    ", HTML.FORM(HTML.Attrs(function() {
      return Spacebars.attrMustache(view.lookup("atts"));
    }), "\n      ", HTML.DIV({
      "class": "row"
    }, "\n        ", Spacebars.With(function() {
      return Spacebars.dataMustache(view.lookup("innerContext"), view.lookup(".."));
    }, function() {
      return [ "\n          ", Blaze._InOuterTemplateScope(view, function() {
        return Blaze._TemplateWith(function() {
          return Spacebars.call(view.lookup("."));
        }, function() {
          return Spacebars.include(function() {
            return Spacebars.call(view.templateContentBlock);
          });
        });
      }), "\n        " ];
    }), "\n      "), "\n    "), "\n  " ];
  });
}));
